.socials {
  list-style: none;
  display: flex;
  padding: 0;
  margin: 0;
}

.socials__item:not(:first-child) {
  margin-left: 30px;
}

.socials__link {
  display: flex;
  outline: 0;
  transition: 0.2s;

  &:hover,
  &:focus,
  &:active {
    opacity: 0.5;
    transition: 0.2s;
  }
}

